html,
body {
    height: 100%
}

div#root {
    height: 100%
}

div.container {
    height: 100%
}

.App {
    text-align: center;
    background-color: #e9f5eb;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

.input-prompt {
    text-align: center;
    font-size: xx-large;
    padding-bottom: 1em;
}

.navigation {
    padding-top: 1em;
    padding-bottom: 1em;
    color: #6E661B;
}

.button-group-container {
    background-color: #88c9A1;
    padding-left: 5%;
    padding-top: 5%;
    padding-bottom: 5%;
    padding-right: 5%;
    align-items: center;
    border-radius: 10px;
}

.submit-button {
    padding-top: 5%;
}

btn.btn-submit {
    padding-top: 10px
}

.input-bill-amount {
    align-items: center;
}

.button-group-container {
 background-color:#88c9A1;
 padding-left: 5%;
 padding-top: 5%;
 padding-bottom: 5%;
 padding-right: 5%;
 align-items: center;
 border-radius: 10px;
 min-width: 30em;
}

.button-row button {
  background-color: white;
  border: 1px solid green;
  /* padding: 10px 24px; */
  cursor: pointer;
  color: #6E661B;
  border-radius: 8px;
  font-size: 16px;
  width: 100%;
  min-height: 100%;
  padding-top: 1em;
  padding-bottom: 1em;
}

.button-row button:hover {
    background-color: #D8F1E1;
}


/* This prevents double-tap zooming on iOS */

touch-action: manipulation;
